import { useGLTF } from '@react-three/drei'

function Plataform(props) {
    const { nodes, materials } = useGLTF('/plataform.glb')
    return (
      <group {...props} dispose={null}>
        <mesh geometry={nodes.Plano018.geometry} material={materials.plataform} position={[-1.61, 0, 1.66]} />
        <mesh geometry={nodes.Plano019.geometry} material={materials.plataform} position={[-1.61, 0, 1.66]} />
        <mesh geometry={nodes.Plano020.geometry} material={materials.metal2} position={[-1.61, 0, 1.66]} />
        <mesh geometry={nodes.Plano021.geometry} material={materials.metal2} position={[-1.61, 0, 1.66]} />
        <mesh geometry={nodes.Plano022.geometry} material={materials.plataform} position={[-1.5, -3.62, 1.49]} />
        <mesh geometry={nodes.Plano025.geometry} material={materials.metal2} position={[-1.61, 0, 1.66]} />
        <mesh geometry={nodes.Plano026.geometry} material={materials.metal2} position={[-1.61, 0, 1.66]} />
        <mesh geometry={nodes.Plano027.geometry} material={materials.metal2} position={[-1.5, -3.62, 1.49]} />
        <mesh geometry={nodes.Plano028.geometry} material={materials.metal2} position={[-1.5, -3.62, 1.49]} />
      </group>
    )
  }
  
  useGLTF.preload('/plataform.glb')

function Store(props) {
    const { nodes, materials } = useGLTF('/store.glb')
    return (
      <group {...props} dispose={null}>
        <mesh castShadow receiveShadow geometry={nodes.Plano018.geometry} material={materials.metal2} />
        <mesh castShadow receiveShadow geometry={nodes.Plano018_1.geometry} material={materials.pared2} material-color="#E7E7E7"/>
        <mesh castShadow receiveShadow geometry={nodes.Plano018_2.geometry} material={materials.metal3} />
        <mesh castShadow receiveShadow geometry={nodes.Plano018_3.geometry} material={materials.metal1} />
      </group>
    )
  }
  
  useGLTF.preload('/store.glb')

function Bus(props) {
    const { nodes, materials } = useGLTF('/bus.glb')
    return (
      <group {...props} dispose={null}>
        <mesh geometry={nodes.Bus.geometry} material={materials.Bus} position={[2.41, 0.19, 0.52]} rotation={[0, 0, -Math.PI / 2]} scale={0.36} material-color="#FA4605">
          <mesh geometry={nodes.Cilindro006.geometry} material={materials.motors} position={[-0.08, 1.84, 0]} rotation={[0, 0, Math.PI / 2]} scale={[2.98, 4.18, 2.98]} material-color="#000"/>
          <mesh geometry={nodes.Cilindro007.geometry} material={materials.motors} position={[-0.08, 1.84, -7.4]} rotation={[0, 0, Math.PI / 2]} scale={[2.98, 4.18, 2.98]} />
          <mesh geometry={nodes.Cilindro008.geometry} material={materials.motors} position={[-0.08, 1.84, -9.41]} rotation={[0, 0, Math.PI / 2]} scale={[2.98, 4.18, 2.98]} />
          <mesh geometry={nodes.Cubo.geometry} material={materials.batery} position={[0.24, 0, 0.16]} rotation={[0, 0, Math.PI / 2]} scale={4.05} />
          <mesh geometry={nodes.Vert.geometry} material={materials.metal5} position={[0.11, 0.39, -1.72]} rotation={[0, 0, Math.PI / 2]} scale={4.05} />
        </mesh>
      </group>
    )
  }
  
  useGLTF.preload('/bus.glb')

function Tower(props) {
    const { nodes, materials } = useGLTF('/tower.glb')
    return (
      <group {...props} dispose={null}>
        <group position={[-0.8, 1.71, -1.66]} scale={1.02}>
          <mesh castShadow receiveShadow geometry={nodes.Cube003_1.geometry} material={materials.pared} material-color="#D3CFC9" />
          <mesh castShadow receiveShadow geometry={nodes.Cube003_2.geometry} material={materials.wrapDark} />
          <mesh castShadow receiveShadow geometry={nodes.Cube003_3.geometry} material={materials.wrapBlack} />
        </group>
      </group>
    )
  }
  
  useGLTF.preload('/tower.glb')

function Ground(props) {
    const { nodes, materials } = useGLTF('/ground.glb')
    return (
      <group {...props} dispose={null}>
        <mesh geometry={nodes.Plano017.geometry} material={materials['Material.002']} scale={30.15} />
      </group>
    )
  }
  
  useGLTF.preload('/ground.glb')

function Lights(props) {
    const { nodes, materials } = useGLTF('/Lights.glb')
    return (
      <group {...props} dispose={null}>
        <group position={[-0.8, 2.42, -1.66]} scale={1.07}>
          <group position={[0, -0.66, 0]} scale={0.96}>
            <mesh geometry={nodes.Cilindro009.geometry} material={materials.clockLight} position={[0, 0, 1.15]} rotation={[Math.PI / 2, 0, 0]} scale={[0.57, 0.1, 0.57]} material-color="#00ffff"/>
            <mesh geometry={nodes.Plano009.geometry} material={materials.manecillaLight} position={[0, 0, 1.17]} rotation={[Math.PI / 2, 0, 0]} scale={0.03} material-color="#FAED27"/>
            <mesh geometry={nodes.Plano010.geometry} material={materials.manecillaLight} position={[0, 0, 1.17]} rotation={[-Math.PI / 2, Math.PI / 3, -Math.PI]} scale={0.03} />
            <mesh geometry={nodes.Plano011.geometry} material={materials.manecillaLight} position={[1.17, 0, 0]} rotation={[Math.PI / 2, 0, -Math.PI / 2]} scale={0.03} />
            <mesh geometry={nodes.Plano012.geometry} material={materials.manecillaLight} position={[1.17, 0, 0]} rotation={[-2.62, 0, -Math.PI / 2]} scale={0.03} />
          </group>
          <group position={[0, -0.66, 0]} rotation={[0, 1.57, 0]} scale={0.96}>
            <mesh geometry={nodes.Cilindro003.geometry} material={materials.clockLight} position={[0, 0, 1.15]} rotation={[Math.PI / 2, 0, 0]} scale={[0.57, 0.1, 0.57]} />
          </group>
        </group>
        <group position={[2.41, 0.19, 0.52]} rotation={[0, 0, -Math.PI / 2]} scale={0.36}>
          <mesh geometry={nodes.Cilindro001.geometry} material={materials.motorLight} position={[-0.08, 1.84, 0]} rotation={[0, 0, Math.PI / 2]} scale={[2.98, 4.18, 2.98]} material-color="#00ffff"/>
          <mesh geometry={nodes.Cilindro004.geometry} material={materials.motorLight} position={[-0.08, 1.84, -7.4]} rotation={[0, 0, Math.PI / 2]} scale={[2.98, 4.18, 2.98]} />
          <mesh geometry={nodes.Cilindro010.geometry} material={materials.motorLight} position={[-0.08, 1.84, -9.41]} rotation={[0, 0, Math.PI / 2]} scale={[2.98, 4.18, 2.98]} />
        </group>
        <mesh geometry={nodes.Bus001.geometry} material={materials.windowsLight} position={[2.41, 0.19, 0.52]} rotation={[0, 0, -Math.PI / 2]} scale={0.36} material-color="#00ffff"/>
        <mesh geometry={nodes.Bus002.geometry} material={materials.LightT} position={[2.41, 0.19, 0.52]} rotation={[0, 0, -Math.PI / 2]} scale={0.36} material-color="red"/>
        <mesh geometry={nodes.Bus003.geometry} material={materials.LightF} position={[2.41, 0.19, 0.52]} rotation={[0, 0, -Math.PI / 2]} scale={0.36} material-color="#FFF"/>
        <mesh geometry={nodes.Logo.geometry} material={materials.Logo} position={[-3.16, 3.47, 4.32]} rotation={[Math.PI / 2, 0, 0]} scale={0.18} material-color="#00ffff"/>
        <mesh geometry={nodes.bgLogo.geometry} material={materials.wrapLight} position={[-3.16, 3.47, 4.14]} rotation={[Math.PI / 2, 0, 0]} scale={0.51} material-color="red"/>
        <mesh geometry={nodes.Plano023.geometry} material={materials.Windows} material-color="#00ffff"/>
        <mesh geometry={nodes.bgLogo001.geometry} material={materials.bgLogo} position={[-3.16, 3.47, 4.14]} rotation={[Math.PI / 2, 0, 0]} scale={0.51} material-color="#3798FA"/>
      </group>
    )
  }
  
  useGLTF.preload('/Lights.glb')
  
export { Plataform, Store, Bus, Tower, Ground, Lights }